import React, { Component } from 'react';
import { NavBar, Icon,Tabs ,Carousel,Grid} from 'antd-mobile';
const data = Array.from(new Array(3)).map((_val, i) => ({
    icon: 'https://gw.alipayobjects.com/zos/rmsportal/nywPmnTAvTmLusPxHPSu.png',
    text: `name${i}`,
  }));
  
  const data1 = Array.from(new Array(3)).map(() => ({
    icon: 'https://gw.alipayobjects.com/zos/rmsportal/WXoqXTHrSnRcUwEaQgXJ.png',
  }))
export default class AppHome extends Component {
    state = {
        data: ['1', '1'],
        imgHeight: 176,
        
    }
    
  render() {
    return (
      <div>
        <NavBar
        style={{background:'#3fcccb',color:'white'}}
        //返回上一页
        // icon={<Icon type="left" />}
        // onLeftClick={() => console.log('onLeftClick')}
        >住吧家居</NavBar>
        
       
        <Carousel
            autoplay={true}
            infinite
            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
            afterChange={index => console.log('slide to', index)}
            >
            {this.state.data.map(val => (
                <a
                key={val}
                href="http://www.alipay.com"
                style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                >
                    
                <img
                    src={require(`../img/${val}.png`)}
                    alt=""
                    style={{ width: '100%', verticalAlign: 'top' }}   //图片记得写上宽度百分之百
                    onLoad={() => {
                    // fire window resize event to change height
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto' });
                    }}
                />
                </a>
            ))}
        </Carousel>
       

        <div style={{ backgroundColor:'#eee',width: '100%', height: '130px'}}>
        <Grid data={data1}
        columnNum={3}
        renderItem={dataItem => (
            <div style={{ padding: '3px',backgroundColor:'#eee'}}>
            <img src={require(`../img/3.png`)} style={{ width: '100px', height: '100px',backgroundColor:'#eee'}} alt="" />
            </div>
        )}
        />
        </div>

        <div style={{marginTop:'12px',width:'100%'}}>
            {/* <a style={{ width: '30px',height:'40px', backgroundColor:'3fcccb',display:'inline'}}></a>
            <span>热门推荐</span> */}
            <img src={require(`../img/7.png`)} style={{ width: '100%', verticalAlign: 'top' }} />
            <img src={require(`../img/2.png`)} style={{ width: '100%', verticalAlign: 'top',marginTop:'14px' }} />
        </div>
        
        
      </div>
    );
  }
}
