import React from 'react';
import { TabBar } from 'antd-mobile';
import AppHome from './Containers/AppHome';
import AppLinggan from './Containers/AppLinggan';
import AppShop from './Containers/AppShop';
import AppWo from './Containers/AppWo';
export default class AppTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'home',
      hidden: false,
      fullScreen: false,
    };
  }

  render() {
    return (
      <div style={{position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="black" //选中颜色
          tintColor="#3fcccb"  //未选中颜色
          barTintColor='white' //背景颜色
          
        >
          <TabBar.Item
            title="首页"
            key="1"
            icon={
              <i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-shouye'></i>
            }
            selectedIcon={<i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-shouye'></i>
            }
            selected={this.state.selectedTab === 'home'}
            // badge={1}
            onPress={() => {
              this.setState({
                selectedTab: 'home',
              });
            }}
            // data-seed="logId"
          >
            {/* {this.renderContent('Life')} */}
            <AppHome/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-dengpao'></i>
            }
            selectedIcon={<i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-dengpao'></i>
            }
            title="灵感"
            key="2"
            // badge={'new'}
            selected={this.state.selectedTab === 'linggan'}
            onPress={() => {
              this.setState({
                selectedTab: 'linggan',
              });
            }}
            // data-seed="logId1"
          >
            
            <AppLinggan/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-shop'></i>
            }
            selectedIcon={<i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-shop'></i>
            }
            title="商城"
            key="3"
            selected={this.state.selectedTab === 'shop'}
            onPress={() => {
              this.setState({
                selectedTab: 'shop',
              });
            }}
          >
            <AppShop/>
            {/* {this.renderContent('Friend')} */}
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-wode'></i>
            }
            selectedIcon={<i style={{fontSize:22,lineHeight:'22px'}} className='iconfont icon-wode'></i>
            }
            title="我的"
            key="4"
            selected={this.state.selectedTab === 'my'}
            onPress={() => {
              this.setState({
                selectedTab: 'my',
              });
            }}
          >
            <AppWo/>
            {/* {this.renderContent('My')} */}
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}